import React from 'react';
import '../css/GalleryArrow.css'
import { isMobile } from "react-device-detect";

class GalleryArray extends React.Component {
    // mouse enter in and arrow event
    MouseEnter = e => {
        if(e.currentTarget === e.target) {
            e.currentTarget.classList.remove("deactive");
            e.currentTarget.classList.add("active");
        }
    }

    // mouse leave out arrow event
    MouseLeave = e => {
        if(e.currentTarget === e.target) {
            e.currentTarget.classList.remove("active");
            e.currentTarget.classList.add("deactive");
        }
    }

    //touch event
    onClick(e, addition){
        e.preventDefault();
        this.props.setImageIdx(addition);
        this.props.scrollTinyImage();
    }

    render() {
        return (
            <div id='arrow-container'>
                <div className='arrow deactive' id='left-arrow'
                    onMouseEnter = {isMobile ? () => (false) : this.MouseEnter}
                    onMouseLeave = {isMobile ? () => (false) : this.MouseLeave}
                    onClick = {(e) => this.onClick(e, -1)}
                ></div>
                <div className='arrow deactive' id='right-arrow'
                    onMouseEnter = {isMobile ? () => (false) : this.MouseEnter}
                    onMouseLeave = {isMobile ? () => (false) : this.MouseLeave}
                    onClick = {(e) => this.onClick(e, 1)}
                ></div>
            </div>
        );
    }
}

export default GalleryArray;